// file post-list.js
import {
  // useState,
  Fragment,
  useEffect,
  useReducer,
  lazy,
  Suspense,
  useCallback,
  useContext,
} from "react";
import { ThemeContext } from "../../App";

import Title from "../../component/title";
import Grid from "../../component/grid";
import Box from "../../component/box";

import PostCreate from "../post-create";
import { Alert, Skeleton, LOAD_STATUS } from "../../component/load";
import { getDate } from "../../util/getDate";

import {
  requestInitialState,
  requestReducer,
  REQUEST_ACTION_TYPE,
} from "../../util/request";
import "../../theme.css";

const PostItem = lazy(() => import("../post-item"));

export default function Container({ onSubmit }) {
  const [state, dispatch] = useReducer(requestReducer, requestInitialState);

  const getData = useCallback(async () => {
    dispatch({ type: LOAD_STATUS.PROGRESS });
    try {
      const res = await fetch("http://localhost:4000/post-list");

      const data = await res.json();

      if (res.ok) {
        dispatch({
          type: REQUEST_ACTION_TYPE.SUCCESS,
          payload: convertData(data),
        });
      } else {
        dispatch({
          type: REQUEST_ACTION_TYPE.ERROR,
          payload: data.message,
        });
      }
    } catch (error) {
      dispatch({
        type: REQUEST_ACTION_TYPE.ERROR,
        payload: error.message,
      });
    }
  }, []);

  const convertData = (raw) => ({
    list: raw.list.reverse().map(({ id, username, text, date }) => ({
      id,
      username,
      text,
      date: getDate(date),
    })),

    isEmpty: raw.list.length === 0,
  });

  useEffect(() => {
    getData();
  }, [getData]);

  const { toggle, theme } = useContext(ThemeContext);
  // const THEME_ACTION_TYPE = { TOGGLE: "toggle" };
  return (
    <div
      style={{
        backgroundColor: `var(--${theme}-accent-color)`,
      }}
    >
      <Grid>
        <div className="header-items">
          <button
            onClick={() => toggle()} //  toggle  
            className="field-form__button"
          >
            Change theme
          </button>
        </div>

        <Box>
          <Grid>
            <Title>Home</Title>
            <PostCreate
              onCreate={getData}
              placeholder="What is happening?!"
              button="Post"
            />
          </Grid>
        </Box>

        {state.status === LOAD_STATUS.PROGRESS && (
          <Fragment>
            <Box>
              <Skeleton />
            </Box>
            <Box>
              <Skeleton />
            </Box>
          </Fragment>
        )}

        {state.status === LOAD_STATUS.ERROR && (
          <Alert status={state.status} message={state.message} />
        )}

        {state.status === LOAD_STATUS.SUCCESS && (
          <Fragment>
            {state.data.isEmpty ? (
              <Alert message="  " />
            ) : (
              state.data.list.map((item) => (
                <Fragment key={item.id}>
                  <Suspense
                    fallback={
                      <Box>
                        <Skeleton />
                      </Box>
                    }
                  >
                    <PostItem {...item} />
                  </Suspense>
                </Fragment>
              ))
            )}
          </Fragment>
        )}
      </Grid>
    </div>
  );
}

// ###  , ,   

// #### 1.   :
// 1. **status**:
//    - ****: State
//    - ****:   : `null`, `LOAD_STATUS.PROGRESS`, `LOAD_STATUS.SUCCESS`,  `LOAD_STATUS.ERROR`.

// 2. **message**:
//    - ****: State
//    - ****:      .

// 3. **data**:
//    - ****: State
//    - ****:   ,   .

// 4. **getData()**:
//    - ****: 
//    - ****:        .       .

// 5. **convertData()**:
//    - ****: 
//    - ****:              .

// #### 2.    :
// 1. **     **:
//    -      `Title`.
//    -       `PostCreate`.

// 2. ** **:
//    -     .
//    -    (`Skeleton`)    .

// 3. **   **:
//    -        .

// ####  :
//  `Container.js`   ,             .         .      ,      .
